@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}
.chat-mid-part{
   
    width: 60%;
    height: 90vh;
    margin: 25px auto;
    background-color: white;
    box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.75);
}
.contact-list{
  width: 30%;
  border-right: 0.002px solid rgba(99, 117, 117, 0.75);
  height: inherit;
  float: left;
  overflow-y: scroll;
  overflow-x: hidden;
}
.top-bar,.bottom-bar{
    float: left;
    width: 100%;
}
.top-bar{
    height: 8vh;
    border-bottom:0.002px solid rgba(99, 117, 117, 0.75);
}
.top-left,.top-right{height: inherit;}
.top-left{width: 30%; border-right: 0.002px solid rgba(99, 117, 117, 0.75);}
.top-right{width: 70%;}
.bottom-bar{
    height: 82vh;
}

::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  ::-webkit-scrollbar-thumb {
    background: gray;
  }
  

  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
.contact-chat{
  width: 70%;
  
  height: inherit;
  float: left;
  overflow-y: scroll;
  overflow-x: hidden;
}
body,html{
    width: 100%;
    height: 100%;
}
.navbr {
    color: black;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: white;
    box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.75);
  }
  
  .navbr-container {
    display: flex;
    justify-content: space-between;
    height: 60px;
  }
  
  .container {
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
  }
  .navItem{color: black;}
  .navbr-logo {
    color: black;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin: auto;
    font-family: 'Satisfy', cursive;
  }
  
  .navbr-icon {
    margin-right: 0.5rem;
  }
  
  .nav-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: end;
    margin: auto;
  }
  
  .nav-item {
    height: 59px;
    border-bottom: 2px solid transparent;
  }
  .grp-input{
    outline: none;
    border: none;
    background-color: #fafafa;
  }
  .input-grp-div{
    border: 1px solid black;
    border-radius: 4px;
   padding: 5px 10px;
   background-color: #fafafa;
  }
  .m-right{margin-right: 200px;}
  .search-icon{
    margin-right: 10px;
    
  }
  .nav-links {
    color: black;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .fa-bars {
   color: black;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 980px) {
    .NavbrItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 60px;
      left: -100%;
      opacity: 1;
      justify-content: start;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: white;
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      color: #f00946;
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
    }
  
    .navbr-logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
     color: black;
      font-size: 2rem;
    }
  
    .nav-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }
  }


